import React, { useState } from "react";
import { WelcomeHero } from "@/components/WelcomeHero";
import { ServiceFeatures } from "@/components/ServiceFeatures";
import { ProgressIndicator } from "@/components/ProgressIndicator";
import { ReadingModeToggle } from "@/components/ReadingModeToggle";
import { FontSizeControl } from "@/components/FontSizeControl";
import { ThemeToggle } from "@/components/ThemeToggle";
import { EnhancedMathRenderer } from "@/components/EnhancedMathRenderer";
import { MathText } from "@/components/MathText";
import { LoadingSpinner } from "@/components/LoadingSpinner";
import { ErrorBoundary } from "@/components/ErrorBoundary";

export const TestPage: React.FC = () => {
  const [showLoading, setShowLoading] = useState(false);
  const [showError, setShowError] = useState(false);

  const testSteps = [
    {
      id: "step1",
      title: " ",
      description: "  ",
      completed: true,
      current: false,
    },
    {
      id: "step2",
      title: "OTP ",
      description: "  ",
      completed: true,
      current: false,
    },
    {
      id: "step3",
      title: " ",
      description: "  ",
      completed: false,
      current: true,
    },
  ];

  const handleTestLoading = () => {
    setShowLoading(true);
    setTimeout(() => setShowLoading(false), 3000);
  };

  const handleTestError = () => {
    setShowError(true);
    setTimeout(() => setShowError(false), 2000);
  };

  if (showError) {
    throw new Error(" !");
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-background via-background to-muted/20">
      <div className="container mx-auto px-4 py-8 space-y-12">
        {/*  */}
        <div className="text-center space-y-4">
          <h1 className="text-4xl font-bold bg-gradient-to-r from-primary via-secondary to-accent bg-clip-text text-transparent">
              
          </h1>
          <p className="text-lg text-muted-foreground">
                 
          </p>
        </div>

        {/* WelcomeHero  */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold">WelcomeHero </h2>
          <WelcomeHero userEmail="test@example.com" showWelcome={true} />
        </div>

        {/* ServiceFeatures  */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold">ServiceFeatures </h2>
          <div className="grid md:grid-cols-2 gap-6">
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
              <h3 className="text-lg font-semibold mb-4"> </h3>
              <ServiceFeatures compact={false} />
            </div>
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
              <h3 className="text-lg font-semibold mb-4"> </h3>
              <ServiceFeatures compact={true} />
            </div>
          </div>
        </div>

        {/* ProgressIndicator  */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold">ProgressIndicator </h2>
          <div className="grid md:grid-cols-2 gap-6">
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
              <h3 className="text-lg font-semibold mb-4"> </h3>
              <ProgressIndicator steps={testSteps} compact={false} />
            </div>
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
              <h3 className="text-lg font-semibold mb-4"> </h3>
              <ProgressIndicator steps={testSteps} compact={true} />
            </div>
          </div>
        </div>

        {/*   */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold"> </h2>
          <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
            <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
              <div className="space-y-2">
                <h3 className="font-semibold"> </h3>
                <ReadingModeToggle />
              </div>
              <div className="space-y-2">
                <h3 className="font-semibold"> </h3>
                <FontSizeControl />
              </div>
              <div className="space-y-2">
                <h3 className="font-semibold"></h3>
                <ThemeToggle />
              </div>
              <div className="space-y-2">
                <h3 className="font-semibold"> </h3>
                <div className="space-y-2">
                  <button
                    onClick={handleTestLoading}
                    className="w-full px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors"
                  >
                     
                  </button>
                  <button
                    onClick={handleTestError}
                    className="w-full px-4 py-2 bg-destructive text-destructive-foreground rounded-lg hover:bg-destructive/90 transition-colors"
                  >
                     
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/*     */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold">  </h2>
          <div className="grid md:grid-cols-2 gap-6">
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
              <h3 className="text-lg font-semibold mb-4">MathText </h3>
              <div className="space-y-4">
                <MathText> : $x^2 + y^2 = z^2$</MathText>
                <MathText> :</MathText>
                <MathText>$$E = mc^2$$</MathText>
                <MathText>{" : $$\\int_{-\\infty}^{\\infty} e^{-x^2} dx = \\sqrt{\\pi}$$"}</MathText>
              </div>
            </div>
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
              <h3 className="text-lg font-semibold mb-4">EnhancedMathRenderer</h3>
              <div className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground mb-2">/  :</p>
                  <EnhancedMathRenderer
                    formula="x = \\frac{-b \\pm \\sqrt{b^2 - 4ac}}{2a}"
                    display={true}
                    zoomable={true}
                    copyable={true}
                    number="1"
                  />
                </div>
                <div>
                  <p className="text-sm text-muted-foreground mb-2"> :</p>
                  <EnhancedMathRenderer
                    formula="\\sum_{i=1}^{n} i = \\frac{n(n+1)}{2}"
                    display={false}
                    zoomable={true}
                    copyable={true}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        {/*    */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold"> </h2>
          <div className="grid md:grid-cols-3 gap-6">
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg text-center">
              <h3 className="text-lg font-semibold mb-4"> </h3>
              <LoadingSpinner size="sm" message=" ..." />
            </div>
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg text-center">
              <h3 className="text-lg font-semibold mb-4"> </h3>
              <LoadingSpinner size="md" message=" ..." />
            </div>
            <div className="rounded-3xl border border-border bg-card p-6 shadow-lg text-center">
              <h3 className="text-lg font-semibold mb-4"> </h3>
              <LoadingSpinner size="lg" message=" ..." />
            </div>
          </div>
        </div>

        {/*   */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold">  </h2>
          <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
              {Array.from({ length: 8 }, (_, i) => (
                <div key={i} className="p-4 bg-primary/10 rounded-lg text-center">
                  <div className="w-8 h-8 bg-primary rounded-lg mx-auto mb-2"></div>
                  <p className="text-sm font-medium"> {i + 1}</p>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/*    */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold">  </h2>
          <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
            <p className="text-muted-foreground mb-4">
                    /  .
            </p>
            <div className="grid md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div className="p-4 bg-primary/10 rounded-lg">
                  <h3 className="font-semibold text-primary">Primary </h3>
                  <p className="text-sm text-muted-foreground">   </p>
                </div>
                <div className="p-4 bg-secondary/10 rounded-lg">
                  <h3 className="font-semibold text-secondary">Secondary </h3>
                  <p className="text-sm text-muted-foreground">   </p>
                </div>
              </div>
              <div className="space-y-4">
                <div className="p-4 bg-muted rounded-lg">
                  <h3 className="font-semibold">Muted </h3>
                  <p className="text-sm text-muted-foreground">   </p>
                </div>
                <div className="p-4 bg-accent/10 rounded-lg">
                  <h3 className="font-semibold text-accent">Accent </h3>
                  <p className="text-sm text-muted-foreground">  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/*   */}
        <div className="space-y-4">
          <h2 className="text-2xl font-semibold"> </h2>
          <div className="rounded-3xl border border-border bg-card p-6 shadow-lg">
            <div className="grid md:grid-cols-2 gap-6">
              <div>
                <h3 className="font-semibold mb-2"> </h3>
                <ul className="space-y-1 text-sm text-muted-foreground">
                  <li>  OTP </li>
                  <li>   </li>
                  <li> KaTeX  </li>
                  <li>  </li>
                  <li>  </li>
                  <li>  </li>
                  <li>  </li>
                  <li>  </li>
                </ul>
              </div>
              <div>
                <h3 className="font-semibold mb-2"> </h3>
                <ul className="space-y-1 text-sm text-muted-foreground">
                  <li> React + TypeScript</li>
                  <li> Tailwind CSS</li>
                  <li> Vite</li>
                  <li> Cloudflare Pages</li>
                  <li> Lucia Auth</li>
                  <li> KaTeX</li>
                  <li>  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/*   */}
      {showLoading && (
        <LoadingSpinner 
          fullScreen={true} 
          size="lg" 
          message="  ..." 
        />
      )}
    </div>
  );
};

export default TestPage;
